import { Component, OnInit , EventEmitter, Output} from '@angular/core';
import { FormGroup, FormControl, FormBuilder, FormGroupDirective } from '@angular/forms';
import { stringify } from 'querystring';
import { NewsService } from '../services/newsservice';


@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

newsForm= new FormGroup({
  News: new FormControl(),
});

  constructor(private fb: FormBuilder, private mynews: NewsService) { }
  onSubmit(data, formDirective: FormGroupDirective){
    const respons = this.mynews.PostNews(this.newsForm.value);
    if(respons){
      alert("Successfully posted News")
      formDirective.resetForm();
      this.newsForm = new FormGroup({
        News: new FormControl(),  
      });
      this.newsForm.reset();
    }
    else{
      alert("Error Posting News")
    }

  }
  
  ngOnInit(): void {
  
}
}