import { Component, OnInit } from '@angular/core';
import { DashboardDataService } from '../services/dashboard-data.service';
import { Router, ActivatedRoute } from '@angular/router';
import { State } from '../model/State';
import { District } from '../model/District';
import { DistrictData } from '../model/DistrictData';
import { Subscription } from 'rxjs';



@Component({
  selector: 'app-tracker',
  templateUrl: './tracker.component.html',
  styleUrls: ['./tracker.component.css']
})
export class TrackerComponent implements OnInit {
  responsereport =[];
  finalresponse =[];
  totaldataonly=[];
  districtreport =[];
  districtresponse=[];
  districtData =[];
  stateList: State[] = [];
  statecode: String;
  currentState: State;
  private termSub = Subscription.EMPTY;
  

  constructor(public dashboarddataservice: DashboardDataService, private router: ActivatedRoute) {
  
   }


  ngOnInit(): void {
    this.dashboarddataservice.sendGetRequest().subscribe(
      data => {
        this.responsereport = data as string [];	 
      console.log(data);
      
    this.responsereport = Object.values(data);
    console.log(this.responsereport);
    this.finalresponse=this.responsereport[1].slice(1,38);
    console.log(this.finalresponse);  
   
    this.totaldataonly=this.responsereport[1].slice(0,1);
    console.log(this.totaldataonly);
    

   })  
   this.dashboarddataservice.getDistrictRequest().subscribe((data) => {
    for (var statename in data) {
      let district: District[] = [];
      var code = data[statename].statecode;
      for (var districtname in data[statename].districtData) {
        let districtdata = data[statename].districtData[districtname] as DistrictData;
        district.push({ name: districtname, data: districtdata });
        
      }

      this.stateList.push({ districtData: district, statecode: code });
     
    }

    this.currentState = this.getStateFromCode(this.statecode);
  })

  this.termSub= this.router.paramMap.subscribe(params => {
    this.statecode = params.get("statecode");
  })

  this.currentState = this.getStateFromCode(this.statecode);

}

getStateFromCode(statecode: String): State {
  return this.stateList.find(state => state.statecode === statecode);
}


   ngOnDestroy() {
     this.termSub.unsubscribe();
   }
  }

  


  
  


		
