import { Injectable } from '@angular/core';
import {  UserApi } from 'src/dashboard/users/users-api';
import { Observable, of } from 'rxjs';
import { Router } from '@angular/router';




@Injectable({
  providedIn: 'root'
})
export class UsersService implements UserApi {


  private loggedInStatus= JSON.parse(localStorage.getItem('loggedIn') ||'false');
  isAuthenticated=false;

  constructor(private router: Router) {}
  setLoggedIn(value: Boolean){
    this.loggedInStatus = value;
    localStorage.setItem('loggedIn', 'true')
  
  }
   
  signIn(username: "username", password: "password") : Observable<any>{
    console.log('UsersService.signIn: ' + username + ' ' + password );
    this.isAuthenticated=true;
    return of({});;
  }
  signOut(): Observable<any>{
    localStorage.setItem("loggedAsAdmin","");
    
    this.isAuthenticated= false;
    this.router.navigate(['/signin']);
    return of({});

    
  }
  access: boolean;

  }
