import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class DashboardDataService {
  private REST_API_SERVER = "https://api.covid19india.org/data.json";
  private District_Response ="https://api.covid19india.org/state_district_wise.json";
  state: string;
  statecode: any;
  districts: any[];

  constructor(private httpClient: HttpClient) { }

  public sendGetRequest(){
    return this.httpClient.get(this.REST_API_SERVER);
  }

  public getDistrictRequest(){
    return this.httpClient.get<any>(this.District_Response);
  }



 
}
