import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TrackerComponent } from './tracker/tracker.component';
import { LatestnewsComponent } from './latestnews/latestnews.component';
import { PrecautionsComponent } from './precautions/precautions.component';
import { AuthenticatedUserComponent } from './authenticated-user/authenticated-user.component';
import { SignInComponent } from 'src/dashboard/users/sign-in/sign-in.component';
import { AdminComponent } from './admin/admin.component';
import { AuthGuardService } from './services/auth-guard.service';



const routes: Routes = [
  

{path: 'signin', component: SignInComponent},
  {path: 'authenticated', component: AuthenticatedUserComponent,canActivate:[AuthGuardService],
  children: [
    {path: '',  canActivateChild: [AuthGuardService],
  
  children:[{path: '', redirectTo:'admin', pathMatch: 'full'},
    {path: 'admin', component:AdminComponent},]}
  ]},
  {path: 'dashboard', component: TrackerComponent},
  {path: 'dashboard/:statecode', component:TrackerComponent},
  {path: 'latestnews', component: LatestnewsComponent},
  {path: 'precautions', component: PrecautionsComponent},
  {path: '', component: TrackerComponent},
  {path: '**', component: TrackerComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
