import { Component } from '@angular/core';
import { ConfigurationService ,ConfigurationServiceSettings} from 'src/dashboard/services/configuration.service';
import { MenuService } from 'src/dashboard/services/menu.service';
import { MyMenuItems } from './app.menu';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  constructor(public configurationService:ConfigurationService, public menuService: MenuService){

    let config :ConfigurationServiceSettings ={
    showlanguageSelector: true,
    showUserControls :true,
    showstatusBarBreakpoint :800
    };

    configurationService.configure(config);

    menuService.items= MyMenuItems;


  }
  
}
